.content{
    display: grid;
    padding: 10px;
    box-sizing: border-box;
    grid-template-columns:4fr;
    background:url(https://themified.com/friend-finder/images/covers/1.jpg) no-repeat ;  
    background-size: 1500px 500px ;
    margin: auto ;
    width:80%;
    grid-template-rows: 6fr 1fr;
  
  }
  
  /****Settings of information of person and his published posts****/
.person{
    display:grid;

    padding: 10px 0 10px 0;
    grid-template-rows:3fr 1fr;
    grid-template-columns: 2fr ;
    width:20%;
    height:40%;
    position:relative;
    top:200px;
  
  }
  .person_photo{
    border-radius:100px;
    height:200px;
    width: 200px;
    overflow:hidden;
    border:solid white 10px ;
    margin: 0 auto;


  }
  .person_photo img{
    height:200px;
    width: 200px;
  }
  .padding{
    padding: 10px;
    display:inline-block;
  
  }
  .info-person{
    width:50px;
    font-size: 24px;
    text-align:right;
    color:#27AAE1;
    margin: 0 auto;
    font-family: sans-serif;


  
  }
  .size_font{
    display: inline;
    font-size: 120%;

  }
  .my_posts{
    font-weight: bolder;
    display: grid;
    margin: 0 auto;
    width: 60%;
    position:relative;
   top: -300px;

  
  }
  
  .form_post_send{
    display:grid;
    grid-template-columns: 3fr 1fr;
    vertical-align: middle;
    margin: 15px;
  
  }
  .form_post_send > input{
    display:grid;
    background-color: #F0F0F0;
     border-radius: 15px;
  }
  .form_post_send  [type = "button"]{
    display:grid;
    grid-column:2/3;
    border-radius: 15px;
    background-color: #27AAE1;
    color:white;
    height:40px;
    vertical-align: middle;
  }
  .user_photo{
    height:50px;
    
  }
   .publ_posts{
    display:grid;
    grid-template-columns: 1fr 11fr;
    position: relative;
   top: -300px;
   
    margin:10px 0 10px 0;
    padding: 10px;
     margin: 0 auto;
    width:60%;
    box-sizing: border-box;
    background-color: #F8F8F8;
    
  }
  .user_photo{
    border-radius: 25px;
    overflow:hidden;
    height:50px;
    width:50px ;
    
  }
  .user_photo>img{
    height:50px;
    width:50px ;
  }
  .block_post{
    width:100%;
    padding: 10px;
  
  }
  .user_text{
    margin-top: 10px;
    max-width:676px;
    min-width:360px;
  
  }
   .like , .dislike {
     display:inline;
    
     text-align: center;
     padding: 5px;
     margin: 10px;
     
    }
    .like{
      color:green;
    }
     .dislike{
      color:red;
    }
    .box_like_dis{
        text-align: right;
        height:50px;
        margin-top: 20px;
    }
    .float{
      display:inline;
      float:right;
    }
    .person_info{
    margin: 0 auto;

    }
    .mini_nav{
      display: grid;
      background-color: black;
      height:40px;
      border: black solid 2px;
       position:relative;
       opacity: 0.5;
    top:350px;
       grid-template-columns: 1fr 1fr 1fr;
       text-align: center;
    }
    .container_info{
      display: grid;
      grid-template-columns: 1fr 3fr;
      
    }
    .link_info{
      margin:10px;
      width: 70px;
      color:white;
      text-align: center;
    }
   
    #overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 1);
    z-index: 999;
    overflow: auto;
    visibility:hidden;
    opacity: 0;
    transition: opacity 0.7s ease-in 0s;
}
.popup {
    top: 10%;
    left: 0;
    right: 0;       
    font-size: 14px;
    margin: auto;
    width: 80%;
    min-width: 200px;
    max-width: 600px;
    position: absolute;
    padding: 15px 20px;
    border: 1px solid #666;
    background-color:white;
    z-index: 1000;
    border-radius: 10px;
    font: 14px/18px 'Tahoma', Arial, sans-serif;
    color:black;
}
.close {
    top: 10px;
    right: 10px;
    width: 32px;
    height: 32px;
    position: absolute;
    border: none;
    border-radius: 50%;
    background-color: rgba(0, 130, 230, 0.9);
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 1), 0 2px 10px 0 rgba(0, 0, 0, 1);
    cursor: pointer;
    outline: none;
}
.close:before {
    color: rgba(255, 255, 255,1);
    content: "X";
    font-family:  Arial, Helvetica, sans-serif;
    font-size: 14px;
    font-weight: normal;
    text-decoration: none;
    text-shadow: 0 -1px rgba(0, 0, 0, 1);
}
.close:hover {
    background-color: rgba(180, 20, 14, 1);
}
